}

.theme__switch-wrapper {
  z-index: 100;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 24px;
  right: 232px;
}

@media screen and (max-width: 768px) {
  .theme__switch-wrapper {
    right: 240px;
  }
}

@media screen and (max-width: 320px) {
  .theme__switch-wrapper {
    top: 20px;
    right: 62px;
  }
}

.theme__switch {
  width: 40px;
  height: 20px;
  margin: 0 10px;
  display: inline-block;
  position: relative;
}

.theme__switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px #0000001a, inset -1px -1px #0000001a, 0 0 4px #4f2ee8;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: var(--white-color);
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  top: 4px;
  left: 1px;
  transform: translate(-1px, -3px);
}

input:checked + .slider, input:focus + .slider {
  background: linear-gradient(#4f2ee8, #686868);
  box-shadow: inset 1px 1px #0000001a, inset -1px -1px #0000001a, 0 0 4px #4f2ee8;
}

input:checked + .slider:before {
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  top: 4px;
  left: 22px;
  transform: translate(-1px, -3px);
}

